<template>
  <div>
    <el-row>
      <el-col :span="5">
        <el-date-picker
          v-model="param.startTime"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </el-col>
      <el-col :span="1">
        <p>
          <font size="4" face="arial"></font>
        </p>
      </el-col>
      <el-col :span="5">
        <el-date-picker
          v-model="param.endTime"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </el-col>
      <el-col :span="5" :offset="5">
        <el-button type="primary" icon="el-icon-search" @click="search()"></el-button>
      </el-col>
    </el-row>

    <table>
      <template v-for="tr in totalByMonthTableVo">
        <tr v-on:click="getMyProduct(tr[0])">
          <template v-for="td in tr">
            <td>{{ td }}</td>
          </template>
        </tr>
      </template>
    </table>

    <!-- -->
    <el-dialog :title="dialogTitle" width="600px" :visible.sync="showMyProductFormVisible">
      <el-form :model="myProduct" ref="editMyProductForm">
        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.bank.name" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.bank.bankName" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.bank.bankCard" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.productType" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <font size="4" face="arial">{{this.Utils.toMoney(myProduct.investmentAmount) }}</font>
          <el-input v-model="myProduct.investmentAmount" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" prop="buyingTime" label-width="150px">
          <el-date-picker
            v-model="myProduct.buyingTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
            :disabled="true"
          ></el-date-picker>
        </el-form-item>

        <el-form-item label=":" prop="interestStartTime" label-width="150px">
          <el-date-picker
            v-model="myProduct.interestStartTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
            :disabled="true"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="profitDate" label-width="150px">
          <el-date-picker
            v-model="myProduct.profitDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
            :disabled="true"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="dueTime" label-width="150px">
          <el-date-picker
            v-model="myProduct.dueTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
            :disabled="true"
          ></el-date-picker>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.expectedInterestRate" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <el-input v-model="myProduct.interestPaymentMethod" :disabled="true" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label=":" label-width="150px">
          <font size="4" face="arial">{{this.Utils.toMoney(myProduct.expectedInterestIncomeMonth) }}</font>
          <el-input
            v-model="myProduct.expectedInterestIncomeMonth"
            :disabled="true"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=":" label-width="150px">
          <el-select v-model="myProduct.state" :disabled="true">
            <el-option
              v-for="item in states"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import option from "./../common/option";
export default {
  data() {
    return {
      totalByMonthVo: {},
      totalByMonthTableVo: {},
      showMyProductFormVisible: false,
      dialogTitle: "",
      states: [
        { value: 3, label: "" },
        { value: 2, label: "" },
        { value: 1, label: "" }
      ],
      myProduct: {
        bank: {
          id: "",
          name: "",
          bankName: "",
          bankCard: ""
        },
        dueTime: "",
        buyingTime: "",
        interestStartTime: "",
        profitDate: ""
      },
      param: {
        startTime: this.Utils.getFirstDayOfYear(new Date()),
        endTime: this.Utils.timeFormat(new Date()),
        bankCard: "",
        transactionTypes: [],
        myProductId: ""
      }
    };
  },
  mounted() {
    //this.getData();
    this.getDataTable();
  },
  methods: {
    drawEcharts() {
      let chart = this.$echarts.init(document.getElementById("echarts"));
      // 
      option.xAxis = this.totalByMonthVo.xaxis;
      option.series = this.totalByMonthVo.series;
      option.legend = this.totalByMonthVo.legend;

      chart.setOption(option);
    },
    getData(param) {
      this.loading = true;
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/bankMyProducts/expectedIncome",
        data: param ? param : {}
      })
        .then(res => {
          if (res.data.code == 0) {
            this.totalByMonthVo = res.data.data;
            this.drawEcharts();
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    getMyProduct(id) {
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/bankMyProducts/selectByPrimaryKey",
        data: { id: id }
      })
        .then(res => {
          if (res.data.code == 0) {
            this.showMyProductFormVisible = true;
            this.myProduct = res.data.data;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    getDataTable(param) {
      this.loading = true;
      this.$http({
        method: "post",
        url: this.BASE_API + "/api/bankMyProducts/expectedIncomeTable",
        data: param ? param : {}
      })
        .then(res => {
          if (res.data.code == 0) {
            this.totalByMonthTableVo = res.data.data;
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    search() {
      this.getDataTable(this.param);
    }
  }
};
</script>

<style lang="scss" scoped>
.echarts-box {
  margin-top: 100px;
  .echarts {
    width: 98%;
    height: 500px;
  }
}

table {
  border-collapse: collapse;

  margin: 0 auto;

  text-align: center;

  margin-top: 30px;

  width: 100%;
}

table td,
table th {
  border: 1px solid #cad9ea;

  color: #666;

  height: 30px;

  word-break: keep-all;
  white-space: nowrap;
}

table thead th {
  background-color: #cce8eb;

  width: 100px;
}

table tr:nth-child(odd) {
  background: #fff;
}

table tr:nth-child(even) {
  background: #f5fafa;
}
</style>



