  static transform(tranvalue) {
    // 
    if (tranvalue) {
      var i = 1;
      var dw2 = new Array("", "", "");// 
      var dw1 = new Array("", "", "");// 
      var dw = new Array("", "", "", "", "", "", "", "", "", "");// 
      //      
      // 
      var source = this.splits(tranvalue);
      var num = source[0];
      var dig = source[1];

      // 
      var k1 = 0;// 
      var k2 = 0;// 
      var sum = 0;
      var str = "";
      var len = source[0].length;// 
      for (i = 1; i <= len; i++) {
        var n = source[0].charAt(len - i);// 
        var bn = 0;
        if (len - i - 1 >= 0) {
          bn = source[0].charAt(len - i - 1);// 
        }
        sum = sum + Number(n);
        if (sum != 0) {
          str = dw[Number(n)].concat(str);// str
          if (n == '0') sum = 0;
        }
        if (len - i - 1 >= 0) {// 
          if (k1 != 3) {// 
            if (bn != 0) {
              str = dw1[k1].concat(str);
            }
            k1++;
          } else {// 
            k1 = 0;
            var temp = str.charAt(0);
            if (temp == "" || temp == "")// 
              str = str.substr(1, str.length - 1);
            str = dw2[k2].concat(str);
            sum = 0;
          }
        }
        if (k1 == 3)// 
        { k2++; }
      }

      //
      var strdig = "";
      if (dig != "") {
        var n = dig.charAt(0);
        if (n != 0) {
          strdig += dw[Number(n)] + "";//
        }
        var n = dig.charAt(1);
        if (n != 0) {
          strdig += dw[Number(n)] + "";//
        }
      }
      str += "" + strdig;
      return str;
    }

  }




