// Echarts
let option = option = {
  title: {
      text: ''
  },
  tooltip: {
      trigger: 'axis'
  },
  legend: {
      data:['','','','','']
  },
  grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
  },
  toolbox: {
      feature: {
          saveAsImage: {}
      }
  },
  xAxis: {
      type: 'category',
      boundaryGap: false,
      data: ['','','','','','','']
  },
  yAxis: {
      type: 'value'
  },
  series: [
      {
          name:'',
          type:'line',
          stack: '',
          data:[120, 132, 101, 134, 90, 230, 210]
      },
      {
          name:'',
          type:'line',
          stack: '',
          data:[220, 182, 191, 234, 290, 330, 310]
      },
      {
          name:'',
          type:'line',
          stack: '',
          data:[150, 232, 201, 154, 190, 330, 410]
      },
      {
          name:'',
          type:'line',
          stack: '',
          data:[320, 332, 301, 334, 390, 330, 320]
      },
      {
          name:'',
          type:'line',
          stack: '',
          data:[820, 932, 901, 934, 1290, 1330, 1320]
      }
  ]
};

export default option



