<template>
  <div class="bank-box">
    <searchs @search="getBanks" />

    <el-table :data="banks" show-summary @selection-change="selectChange" style="width: 100%">
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="bank" label=""></el-table-column>
      <el-table-column prop="bankCard" label=""></el-table-column>
      <el-table-column prop="cashAamount" label=""></el-table-column>
      <el-table-column prop="investmentAmount" label=""></el-table-column>
      <el-table-column prop="accountBalance" label=""></el-table-column>
      <el-table-column label="" fixed="right" width="300">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            @click="investmentDetails(scope.$index, scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="primary"
            plain
            @click="operationLog(scope.$index, scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      :page-sizes="[10, 20, 30, 50]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="400"
    ></el-pagination>
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="bankFormVisible"
      @close="resetForm('bankForm')"
    >
      <el-form :model="bank" :rules="rules" ref="bankForm">
        <el-form-item label="" prop="name" label-width="50px">
          <el-input v-model="bank.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-input v-model="bank.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-input v-model="bank.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="50px">
          <el-date-picker
            v-model="bank.date"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="bankFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitBank('bankForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import searchs from "@/components/search/search";
export default {
  data() {
    return {
      banks: [],
      bank: {
        id: "",
        date: "",
        name: "",
        phone: "",
        address: "",
        status: 0
      },
      bankBackup: Object.assign({}, this.bank),
      multipleSelection: [],
      bankFormVisible: false,
      dialogTitle: "",
      rowIndex: 9999,
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 5, message: " 2  5 ", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getBanks();
  },
  components: {
    searchs
  },
  props: ["param"],
  methods: {
    getBanks(param) {
      this.loading = true;
      this.$http("/api/banks")
        .then(res => {
          this.banks = res.data.filter(item => {
            if (param) {
              if ("" != param.name && "" != param.bank) {
                return item.name == param.name && item.bank == param.bank;
              }
              if ("" != param.name) {
                return item.name == param.name;
              }
              if ("" != param.bank) {
                return item.bank == param.bank;
              }
              return true;
            } else {
              return true;
            }
          });
        })
        .catch(err => {
          console.error(err);
        });
    },
    investmentDetails(index, row) {
      this.$message({
        showClose: true,
        message: ""
      });
    },
    submitBank(formName) {
      // 
      this.$refs[formName].validate(valid => {
        if (valid) {
          let id = this.bank.id;
          if (id) {
            // id-
            this.banks.splice(this.rowIndex, 1, this.bank);
          } else {
            // id-
            this.bank.id = this.banks.length + 1;
            this.banks.unshift(this.bank);
          }
          this.bankFormVisible = false;
          this.$message({
            type: "success",
            message: id ? "" : ""
          });
        }
      });
    },
    operationLog(index, row) {
      this.$message({
        showClose: true,
        message: ""
      });
    },
    resetForm(formName) {
      this.$refs[formName].clearValidate();
    },
    mulDelete() {
      let len = this.multipleSelection.length;
      if (len === 0) {
        this.$message({
          type: "warning",
          message: ""
        });
      } else {
        this.$confirm(` ${len} `, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: `${len}`
            });
          })
          .catch(() => {
            console.log("");
          });
      }
    },
    selectChange(val) {
      this.multipleSelection = val;
    },
    handleAdd() {
      this.dialogTitle = "";
      this.bank = Object.assign({}, this.bankBackup);
      this.bankFormVisible = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.bank-box {
  width: 100%;
  .tool-box {
    padding: 10px 10px;
    border-bottom: 1px solid #eee;
  }
  .el-pagination {
    margin-top: 20px;
  }
}
</style>



